<template>
    <span>
        <AppBarPage>
        </AppBarPage>

        <!-- <v-row no-gutters style="margin-bottom:2px;">
            <v-col cols="12">
                <v-icon icon="mdi-tools" size="16" color="#3F51B5"></v-icon>
